
import NotFound from '../NotFound/NotFound';
import Popup from 'vux/src/components/popup/index.vue'
import Range from 'vux/src/components/range/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'TeamSurplus',
components: {
Popup,
Range,
XHeader,
NotFound,
Datetime,
Group,
XButton
},
data() {
return {
topMenu:[{'memuName':'',menuUrl:'/teamInfo'},{'memuName':'',menuUrl:'/teamOrder'},
{'memuName':'',menuUrl:'/teamsurplus'},{'memuName':'',menuUrl:'/teamSurplus'},
{'memuName':'',menuUrl:'/teamUser'},{'memuName':'',menuUrl:'/teamReg'}],
show:false,
queryShow:false,
list: [], // info
startDate:this.DataTime.getDay(-1),
endDate:this.DataTime.getDay(0),

surplusDetailShow:false,
surplusInde:-1,

index:3,
queryUserId:''

};
},
async created() {
this.$vux.loading.show({
text: 'Loading'
});
await this.getTeamSurplusInfo();
this.$vux.loading.hide();
},
methods: {
async getTeamSurplusInfo() {

let res = await this.$http('/queryUserTeamSurplusInfo', {
body: {
startDate: this.startDate.replace(/-/g,''),
endDate: this.endDate.replace(/-/g,''),
queryUserId:this.queryUserId
}
});
if(this.queryShow){
this.queryShow=!this.queryShow;
}
this.list = res.returnList;

},

leaveSurplusDetail() {
document.body.style.overflow = 'auto';
this.surplusDetailShow = false;
},

openSurplusDetail(v){
this.surplusInde=v;
this.surplusDetailShow=true;
},

queryLowerUserInfo(lowerUserId){
this.surplusDetailShow=false;
this.queryUserId=lowerUserId;
this.surplusInde=-1;
this.getTeamSurplusInfo();
}
}
};



